import { Component, h, Prop, State, Element } from '@stencil/core';

@Component({
      tag: 'ks-order-confirmation-form'
})
export class OrderForm {

    @Prop() action: string;
    @Prop() method: string;
    @Prop() submitString: string;

    @Element() root: HTMLElement;

    @State() loading: boolean = false;

	render() {

		return(
            <form action={this.action} method={this.method} onSubmit={(event)=>this.Submit(event)}>
                <slot/>

                <div class="uk-position-relative">
                    <button type="submit" formnovalidate
                        class="uk-button uk-button-danger uk-width-1-1 uk-width-expand@m ks-text-decorated"
                        style={{fontSize: "28px", fontWeight: "700", padding: "20px"}}>
                        
                        {this.submitString}
                    </button>

                    {this.loading ?
                        <div class="uk-overlay uk-light uk-padding-remove uk-position-cover uk-animation-fade"
                            style={{animationDuration: "0.3s", backgroundColor: "#e21334"}}>
                            <div uk-spinner="ratio: 1.5" class="uk-position-center"></div>
                        </div>
                    : null}
                </div>

                <ks-order-form-modal></ks-order-form-modal>
            </form>
		);
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const inputs = form.querySelectorAll("ks-input-dynamic, ks-order-document-select, ks-input-text, ks-input-textarea, ks-input-check, ks-order-inpost, ks-order-pocztapunkt, ks-order-dpd");

        let highestPosition = Number.MAX_VALUE;

        let valid = true;
        for (let i = 0; i < inputs.length; i++) {
            const input = inputs[i] as any;
            
            if(input.closest("ks-input-dynamic *") != null) continue;

            await input.Validate();
            if(!await input.IsValid()) {
                const inputTop = (input as HTMLElement).getBoundingClientRect().top
                    - document.body.getBoundingClientRect().top;

                if(inputTop < highestPosition)
                    highestPosition = inputTop;

                valid = false;
            }
        }

        if(valid) {
            this.loading = true;
            form.submit();
        }

        else {
            try {
                window.scroll({top: highestPosition -100, behavior: "smooth"});
            }
            catch(error) {
                window.scroll(0, highestPosition -100);
            }
        }
    }

    ShowPrompt(message: string) {
        const prompt = this.root.querySelector("ks-order-form-modal") as any;
        prompt.setAttribute("message", message);
        prompt.Show();
    }
}
